export const EMAIL_REGEX = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
export const FLOAT_NUM_REGEX = /\d+(\.\d+)?(?=$| )|\.\d+(?=$| )|\d+(\.)?(?=$| )$/;
export const FLOAT_NUM_WITH_NEG_REGEX = /(?<=^| )[-]?\d+(\.\d+)?(?=$| )|(?<=^| )[-]?\.\d+(?=$| )|(?<=^| )[-]?\d+(\.)?(?=$| )$/;
