import {ErrorHandler} from '@angular/core';
import {Injectable} from '@angular/core';
import {FNErrorLogSvcs} from './fn-error.log.svcs';

// export interface LoggingErrorHandlerOptions {
//   rethrowError: boolean;
//   unwrapError: boolean;
// }

// // tslint:disable-next-line: variable-name
// export let LOGGING_ERROR_HANDLER_OPTIONS: LoggingErrorHandlerOptions = {rethrowError: false, unwrapError: false};


@Injectable()
export class FNErrorHandler implements ErrorHandler {

  protected errorLogService: FNErrorLogSvcs;

  // private options: LoggingErrorHandlerOptions;

  constructor(errorLogService: FNErrorLogSvcs) {
    this.errorLogService = errorLogService;
  }

  // I handle the given error.
  public handleError(error: any): void {
    // Log to the console. or Send to the error-logging service.
    try {
      this.errorLogService.logError(error);

    } catch (handlingError) {
      console.group('ErrorHandler');
      console.warn('Error when trying to output error.');
      console.error(handlingError);
      console.groupEnd();

    }

  }

}


// I am the collection of providers used for this service at the module level.
// Notice that we are overriding the CORE ErrorHandler with our own class definition.
// --
// CAUTION: These are at the BOTTOM of the file so that we don't have to worry about
