import {FnSession, FnSessionState} from './fn.session.model';
import {createFeatureSelector, createSelector} from '@ngrx/store';
import * as fnSessionAction from '../action/fn-seesion.action';
import {FnSessionStorage} from '../fn-session.storage';

let _initalSession = FnSessionStorage.getFnSession();
_initalSession = _initalSession || {
  ssPK: 0,
  sccPK: 0,
  scaPK: 0,
  saPK: 0,
  scPK: 0,
  scuPK: 0,
  sauPK: 0,
  wIndex: 0,
  pid: null,
  pmid: null,
  mid: null,
  sgaPK: 0,
  baseUrl: null
};
const initialFnSessionState: FnSessionState = {
  currentSession: _initalSession,
  error : ''
};

const getSessionFeatureState = createFeatureSelector<FnSessionState>('fnSession');

export const getCurrentSession = createSelector(
  getSessionFeatureState,
  state => state.currentSession // may be we filter some item
);
export const getCurrent_ssPK = createSelector(
  getCurrentSession,
  state => state.ssPK
);
export const getCurretn_sccPK = createSelector(
  getCurrentSession,
  state => state.sccPK
);
export const getCurrent_scaPK = createSelector(
  getCurrentSession,
  state => state.scaPK
);

export const getCurrent_saPK = createSelector(
  getCurrentSession,
  state => state.saPK
);
export const getCurrent_scPK = createSelector(
  getCurrentSession,
  state => state.scPK
);
export const getCurrent_scuPK = createSelector(
  getCurrentSession,
  state => state.scuPK
);
export const getCurrent_sauPK = createSelector(
  getCurrentSession,
  state => state.scaPK
);
export const getCurrent_wIndex = createSelector(
  getCurrentSession,
  state => state.scaPK
);

export const getCurrent_pid = createSelector(
  getCurrentSession,
  state => state.pid
);
export const getCurrent_pmid = createSelector(
  getCurrentSession,
  state => state.pmid
);

export const getCurrent_mid = createSelector(
  getCurrentSession,
  state => state.mid
);

export const getCurrent_sgaPK = createSelector(
  getCurrentSession,
  state => state.sgaPK
);

export const getCurrent_baseUrl = createSelector(
  getCurrentSession,
  state => state.baseUrl
);


export function fnSessionReducer(
  state = initialFnSessionState,
  action: fnSessionAction.FnSessionActions
): FnSessionState {
  switch (action.type) {

    case fnSessionAction.FnSessionActionType.InitialzeCurrentSession:
      return {
        ...state
      };

    case fnSessionAction.FnSessionActionType.LoadSessionSuccess:
      return {
        ...state,
        currentSession: action.payLoad
      };

    case fnSessionAction.FnSessionActionType.LoadSessionError:
      return {
        ...state,
        error: action.error
      };
    default:
      return state;
  }
}
