import {Component, Input, Output, EventEmitter, OnInit, ViewEncapsulation} from '@angular/core';


@Component({
  selector: 'fn-pagination',
  templateUrl: 'fn-pagination.component.html',
  styleUrls: ['fn-pagination.componen.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FnPaginationComponent implements OnInit {

  @Input() currentPage = 0;
  @Input() totalPage = 0;
  @Input() pageSize = 0;
  @Input() pageClass = '';
  @Output() changePage = new EventEmitter();

  ngOnInit() {
    this.currentPage = 1;
  }

  firstPage() {
    this.currentPage = 1;
    this.changePage.emit(this.currentPage);
  }

  prevPage() {
    this.currentPage = this.currentPage - 1;
    this.changePage.emit(this.currentPage);
  }

  nextPage() {
    this.currentPage = this.currentPage + 1;
    this.changePage.emit(this.currentPage);
  }

  lastPage() {
    this.currentPage = this.totalPage;
    this.changePage.emit(this.currentPage);
  }


}
