import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {mergeMap, map, catchError} from 'rxjs/operators';
import {of, Observable} from 'rxjs';
import {
  FnMenuActionType,
  FnMenuActions,
  LoadMenuData,
  LoadMenuDataSucess,
  LoadMenuDataError
} from '../actions/fn-menu.action';
import {Action} from '@ngrx/store';
import {FnMenuStoreService} from '../service/fn-menu.store.service';

@Injectable()
export class FnMenuEffects {
  constructor(private actions$: Actions, private fnMenuSvcs: FnMenuStoreService) {
  }

  @Effect()
  loadMenuData$ = this.actions$.pipe(
    ofType(FnMenuActionType.LoadMenuData),
    map((action: LoadMenuData) => action.payload),
    mergeMap(action =>
      this.fnMenuSvcs.getMenuData(action).pipe(
        map(menuData => {
          // tslint:disable-next-line: prefer-const
          let menuItemMap = {};
          menuItemMap[menuData.id] = {};
          // tslint:disable-next-line: prefer-const
          let menuItemDetailMap = {};
          menuItemDetailMap[menuData.id] = menuData;
          this.fnMenuSvcs.simpleMenuMapOnId(menuItemMap, menuData.menuItemArray);
          this.fnMenuSvcs.getMenuMapOnId(menuItemDetailMap, menuData.menuItemArray);
          return new LoadMenuDataSucess(menuData, menuItemMap, menuItemDetailMap, true);
        })
        ,
        catchError(err => of(new LoadMenuDataError(err)))
      ))
  );

  // @Effect()
  // loadMenuData$: Observable<Action> = this.actions$.pipe(
  //     ofType(productActions.ProductActionTypes.Load),
  //     mergeMap(action =>
  //       this.productService.getProducts().pipe(
  //         map(products => (new productActions.LoadSuccess(products))),
  //         catchError(err => of(new productActions.LoadFail(err)))
  //       )
  //     )
  //   );

}
