import {Action} from '@ngrx/store';
import {FnMenuItemState} from '../..';

export enum FnMenuActionType {
  InitMenuState = '[FnMenu] initialize Menu State',
  LoadMenuData = '[FnMenu] Load Menu Data',
  LoadMenuDataSucess = '[FnMenu] Load Success',
  LoadMenuDataError = '[FnMenu] Load Error',
  SetActiveMenuId = '[FnMenu] Set Active Menu ID'
}

export class InitMenuState implements Action {
  readonly type = FnMenuActionType.InitMenuState;
}

export class LoadMenuDataSucess implements Action {
  readonly type = FnMenuActionType.LoadMenuDataSucess;

  constructor(public menuItemState: FnMenuItemState, public menuItemMap: object,
              public menuItemDetailMap: object, public isMenuLoaded: boolean) {
  }
}

export class LoadMenuData implements Action {
  readonly type = FnMenuActionType.LoadMenuData;

  constructor(public payload: string) {
  }
}

export class LoadMenuDataError implements Action {
  readonly type = FnMenuActionType.LoadMenuDataError;

  constructor(public error: string) {
  }
}

export class SetActiveMenuId implements Action {
  readonly type = FnMenuActionType.SetActiveMenuId;

  constructor(public activeMenuId: string) {
  }
}

export type FnMenuActions = InitMenuState |
  LoadMenuDataSucess |
  LoadMenuData |
  LoadMenuDataError |
  SetActiveMenuId;
