import { Component, OnInit, Input, forwardRef, ViewEncapsulation } from '@angular/core';
import { FnUiFieldMetaDataService } from './service/fn-ui-field-metadata.service';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { FnInputValueAccessor } from '../../core/model/fn-input.value.accessor';
import { FnInputModel } from '../../core/model/fn-input.model';
import { FN_INPUT_TYPES } from '../../core/model/fn-form.constant';

@Component({
  selector: 'fn-input',
  templateUrl: 'fn-input.component.html',
  styleUrls: ['fn-input.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FnInputComponent),
      multi: true
    }
  ]
})
export class FnInputComponent extends FnInputValueAccessor<any> implements OnInit {

  // @ts-ignore
  inputTypeMap = FN_INPUT_TYPES;

  type = '';
  config: FnInputModel;
  pattern?: any;

  constructor(private fieldMetaDataSvcs: FnUiFieldMetaDataService) {
    super();
  }

  @Input() name = '';
  @Input() isDisabled = false;
  @Input() autocomplete = 'off';
  @Input() tabindex = 0;
  @Input() textAlign: 'left' | 'right' | 'center' | 'justify' = 'left';
  @Input() placeholder = '';
  @Input() parentForm: any;
  @Input() dataKey = '';

  ngOnInit() {
    this.config = this.fieldMetaDataSvcs.getFieldForId(this.dataKey);
    this.type = this.config.typeID.iid;
  }


}


