/*
 * tableAction ->
 *  'ADD' -> Add Action
 *  'REFRESH' -> Refresh Action
 *
*/

import {Subject} from 'rxjs';

export interface TableAction {
  action: string;
  data: any;
}

export class FnTableConfig {
  static defalutSortClass = ' fa-sort';
  static defaultSortValue = 1; // -1 = desc , 1 = asc
  static defaultIsColSort = true;

  uniqueKey ?: any;
  tableClass ?: string;
  pagination ? = true;
  filterOn ? = true;
  hideAction ? = false;
  hideEdit ? = false;
  hideDelete ? = false;
  defaultSortKey?: string;
  hidePagination?: boolean;
  isSelectable?: boolean;

  tableAction: Subject<TableAction>;
}
