import {Injectable} from '@angular/core';
import {FnInputModel} from '../../../core/model/fn-input.model';

@Injectable()
export class FnUiFieldMetaDataService {

  uiFieldMap: object;

  getFieldForId(uiFieldKey: string) {
    return this.uiFieldMap[uiFieldKey];
  }

  setFieldForId(uiFieldKey: string, uiFieldVal: FnInputModel) {
    this.uiFieldMap[uiFieldKey] = uiFieldVal;
  }

  setFieldMap(uiFieldMap: object) {
    this.uiFieldMap = uiFieldMap;
  }

  getFieldMap() {
    return this.uiFieldMap;
  }
}
