import {
  Component,
  ElementRef,
  Input,
  TemplateRef,
  ViewChild,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewRef,
  OnDestroy,
  AfterContentInit,
  ViewEncapsulation
} from '@angular/core';
import {ContentObserver} from '@angular/cdk/observers';
import {Subject} from 'rxjs';
import {startWith, takeUntil} from 'rxjs/operators';


export type FnButtonType = 'primary' | 'success' | 'danger' | 'default' | 'link';
export type FnButtonShape = 'circle' | 'round' | null;

@Component({
  selector: 'fn-button',
  templateUrl: './fn-button-component.html',
  styleUrls: ['fn-button-component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None
})
export class FnButtonComponent implements OnDestroy, AfterContentInit {

  @Input() id = '';
  @Input() type: FnButtonType = 'default';
  @Input() shape: FnButtonShape = null;
  @Input() text = '';
  @Input() iconAddonBefore = '';
  @Input() iconAddonAfter = '';
  @Input() height = 'auto';
  @Input() width = 'auto';
  @Input() disabled = null;
  @Input() flatBorder = false;
  @Input() template: TemplateRef<any>;
  @Input() outline = false;
  @Input() isLoading = false;
  @Input() indexShortcut = 0;

  @Input() set textLoading(text) {
    this._textLoading = text;
  }

  get textLoading() {
    return this._textLoading;
  }

  // @Output() click: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('fnbutton', {static: true}) buttonElement: ElementRef;

  private _textLoading: string;
  private destroy$ = new Subject();

  constructor(private elementRef: ElementRef, private cdr: ChangeDetectorRef, private contentObserver: ContentObserver) {
  }

  ngAfterContentInit() {
    this.contentObserver
      .observe(this.buttonElement)
      .pipe(
        startWith(true),
        takeUntil(this.destroy$)
      )
      .subscribe(() => {
        Promise.resolve().then(() => this.checkContent());
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  checkContent(): void {
    if (!(this.cdr as ViewRef).destroyed) {
      this.cdr.detectChanges();
    }
  }
}

