import {
  Component,
  EventEmitter,
  Output,
  ElementRef,
  Input,
  OnInit,
  ChangeDetectionStrategy,
  ViewEncapsulation
} from '@angular/core';
import {debounceTime} from 'rxjs/operators';
import {Subject} from 'rxjs';

@Component({
  selector: 'fn-table-search',
  templateUrl: 'fn-table.search.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None
})
export class FnTableSearchComponent implements OnInit {

  public debouncer: Subject<string> = new Subject();
  @Output() search = new EventEmitter<string>();
  @Input() config: { actionClear: Subject<any> };

  constructor(private el: ElementRef) {
    this.debouncer.pipe(debounceTime(200)).subscribe(val => {
      this.search.emit(val);
    });

  }

  ngOnInit(): void {
    this.config.actionClear.subscribe(() => {
      this.el.nativeElement.lastChild.value = '';
    });
  }

  debounce(value) {
    this.debouncer.next(value);
  }
}
