import {Component, OnDestroy, ViewEncapsulation} from '@angular/core';
import {FnMenuItemState} from '../fn-stores/models/fn-menu.state';
import {Store, select} from '@ngrx/store';
import {takeUntil} from 'rxjs/operators';
import {getActiveMenuItem} from '../fn-stores/reducer/fn-menu.reducer';
import {FnMenuService} from '../fn-menu.service';
import {Subject} from 'rxjs';

@Component({
  encapsulation: ViewEncapsulation.None,
  templateUrl: 'fn-menu-landing.component.html',
  styleUrls: ['fn-menu-landing.component.scss']
})
export class FnMenuLandingComponent implements OnDestroy {


  activeMenuItem: FnMenuItemState;
  isLoaded: boolean;
  isCustMenu: boolean;
  private destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(private menuStore: Store<FnMenuItemState>, private fnMenuService: FnMenuService) {
    this.loadSubMenuArrayForActiveMenuId();
  }

  loadSubMenuArrayForActiveMenuId() {
    this.menuStore.pipe(
      takeUntil(this.destroy$),
      select(getActiveMenuItem)
    ).subscribe(
      (menuItem: FnMenuItemState) => {
        this.activeMenuItem = menuItem;
        //  console.log(menuItem);
        // tslint:disable-next-line: triple-equals
        this.isCustMenu = this.activeMenuItem.menuType == 'CUSTOM';
        this.isLoaded = true;
      }
    );
  }

  menuClickHandler(menuItem: FnMenuItemState) {
    this.fnMenuService.onMenuItemClick(menuItem.id);
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
}
