import {Component, Input, Output, EventEmitter, forwardRef, ViewEncapsulation, OnInit} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FnInputValueAccessor} from '../../../core';

@Component({
  selector: 'fn-select',
  templateUrl: 'fn-select.component.html',
  styleUrls: ['fn-select.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FnSelectComponent),
      multi: true
    }
  ]
})
export class FnSelectComponent extends FnInputValueAccessor<string> implements OnInit {

  constructor() {
    super();
  }

  @Input() name = '';
  @Input() id = '';
  @Input() isDisabled = false;
  @Input() isErrorBox = true;
  @Input() optionList: Array<any> = [];
  @Input() labelKey = '';
  @Input() inputKey = '';
  @Input() placeholder = '';
  @Input() isRequired = false;
  @Input() parentForm: any;
  @Input() tabIndex = 0;
  @Input() groupBy = '';

  @Output() changeEvent: EventEmitter<any> = new EventEmitter();
  @Output() closeEvent: EventEmitter<any> = new EventEmitter();

  ngOnInit(): void {
    this.setOption();
  }

  setOption() {
    this.placeholder = this.placeholder || this.name;
  }
}
