import {Injectable, TemplateRef} from '@angular/core';
import {MatDialog, MatDialogRef, MatDialogConfig} from '@angular/material';
import {FnConfirmModalComponent} from './fn-confirm-modal.component';
import {DialogData} from '../fn-dialog.model';
import {ComponentType} from '@angular/cdk/portal';

@Injectable()
export class FnModalService {

  constructor(private dialog: MatDialog) {

  }

  public openConfirmModal(dialogData: DialogData = {title: 'Confirmation', content: 'Do you want to delete this?'}):
    MatDialogRef<FnConfirmModalComponent, any> {
    return this.dialog.open(FnConfirmModalComponent, {
      data: dialogData
    });
  }

  public openCustomModal(componentOrTemplateRef: ComponentType<any> | TemplateRef<any>, config?: MatDialogConfig) {
    return this.dialog.open(componentOrTemplateRef, config);
  }

  public openModal(componentOrTemplateRef: ComponentType<any> | TemplateRef<any>, config?: MatDialogConfig) {
    return this.dialog.open(componentOrTemplateRef, {
      data: config.data,
      width: '500px',
      hasBackdrop: config.hasBackdrop
    });
  }
}
