import { AbstractControl, ValidatorFn } from '@angular/forms';
import { EMAIL_REGEX, VALIDATOR_MAP } from './fn-form.constant';
import { FnInputModel } from './fn-input.model';
import { FnI18nService } from '../../i18n/fn-i18.service';

export function FnPatternValidator(i18nSvcs: FnI18nService, config: FnInputModel): ValidatorFn {
  return (control: AbstractControl) => {
    const pattern = new RegExp(`^${config.pattern}$`);
    if ((pattern).test(control.value)) {
      return null;
    } else {
      return { valid: false, message: i18nSvcs.errorMessage(VALIDATOR_MAP.EMAIL, config.key) };
    }
  };
}

export function FnEmailValidator(i18nSvcs: FnI18nService, config: FnInputModel): ValidatorFn {
  return (control: AbstractControl) => {
    const pattern = config.pattern === undefined ? EMAIL_REGEX : new RegExp(`^${config.pattern}$`);
    if ((pattern).test(control.value)) {
      return null;
    } else {
      return { valid: false, message: i18nSvcs.errorMessage(VALIDATOR_MAP.EMAIL, config.key) };
    }
  };
}

export function FnRequireValidator(i18nSvcs: FnI18nService, config: FnInputModel): ValidatorFn {
  return (control: AbstractControl) => {
    if (control.value !== undefined && control.value !== '') {
      return null;
    } else {
      return { valid: false, message: i18nSvcs.errorMessage(VALIDATOR_MAP.REQUIRED, config.key) };
    }
  };
}

export function FnMinValidator(i18nSvcs: FnI18nService, config: FnInputModel): ValidatorFn {
  return (control: AbstractControl) => {
    const value = control.value || '';
    if (config.isRequired && value.length >= config.minLength) {
      return null;
    } else {
      return { valid: false, message: i18nSvcs.errorMessage(VALIDATOR_MAP.MIN_LENGTH, config.key) };
    }
  };
}

export function FnMaxValidator(i18nSvcs: FnI18nService, config: FnInputModel): ValidatorFn {
  return (control: AbstractControl) => {
    const value = control.value || '';
    if (config.maxLength >= value.length) {
      return null;
    } else {
      return { valid: false, message: i18nSvcs.errorMessage(VALIDATOR_MAP.MIN_LENGTH, config.key) };
    }
  };
}


