import * as StackTrace from 'stacktrace-js';
import {FnBrowserDetail} from '../helpers/fn-browser.detail';

export class FNExceptionJson {
  id: string;
  appID = 'HW-Web';
  appVer: any;
  language = 'Javascript';
  langVersion: any;
  deviceType = 'Web';
  deviceVersion: any = ''; // systemObject.browserMajorVersion;
  deviceBrand = ''; // systemObject.browser;
  deviceTime: any = new Date();
  osType: any = ''; // systemObject.os;
  osVersion: any = ''; // systemObject.os+" "+systemObject.osVersion;
  page: string;
  custUserEmailID: string;
  customerEmailID: string;
  siteMainID: string;
  custUserPK: any;
  customerPk: any;
  sitePk: any;
  expMessage: string;
  rootExpLineNum: number;
  rootStackTrace: string;
  expStackTrace: any;
  selectedObjectHash: any = {};
  className: string; //
  methodName: string; //
  categoryName: string; //
  threadName: string; //
  lastAction: string; //
  severity: string; //

  constructor(exception: any) {
    this.init(exception);
  }

  init(exception: any) {
    const browserDetail = new FnBrowserDetail().browserDetail();
    this.deviceVersion = browserDetail.browserMajorVersion;
    this.deviceBrand = browserDetail.browser;
    this.deviceTime = new Date();
    this.osType = browserDetail.os;
    this.osVersion = this.osType + ' ' + browserDetail.osVersion;
    this.expMessage = exception.message;
    StackTrace.fromError(exception).then((_stackTrace) => {
      const _rootStackTrak = _stackTrace[0];
      this.expStackTrace = _stackTrace.join('\n');
      this.rootStackTrace = _rootStackTrak.toString();
      this.rootExpLineNum = _rootStackTrak.lineNumber;
      this.id = this.expMessage + this.expStackTrace;

    }).catch((_err) => {
      this.expStackTrace = exception.toString();
      this.rootStackTrace = exception.toString();
      this.id = this.expMessage + this.expStackTrace;
    });

  }
}


