import {Component, OnInit, EventEmitter, Input, Output, ViewEncapsulation, forwardRef} from '@angular/core';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {FnInputValueAccessor} from '../../core/model/fn-input.value.accessor';

@Component({
  selector: 'fn-color-picker',
  templateUrl: 'fn-color-picker.component.html',
  styleUrls: ['fn-color-picker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FnColorPickerComponent),
      multi: true
    }
  ],
  encapsulation: ViewEncapsulation.None
})
export class FnColorPickerComponent extends FnInputValueAccessor<any> implements OnInit {


  @Input() isDisable = false;
  @Input() isDisableInput = true;
  @Input() showOkBtn = true;
  @Input() showCancelBtn = true;
  @Input() okButtonClass = 'btn btn-primary btn-xs';
  @Input() cancelButtonClass = 'btn btn-danger btn-xs';
  @Input() isSaveClkOutside = false;
  @Input() outputFormat = 'hex';
  @Output() changeColorCb: EventEmitter<string> = new EventEmitter();

  ngOnInit() {
    this.okButtonClass = this.okButtonClass + ' ok-button pull-right btn-round';
    this.cancelButtonClass = this.cancelButtonClass + ' cancel-button pull-left btn-round';
  }

  changeColor(colorCode: string) {
    this.value = colorCode;
    this.changeColorCb.emit(colorCode);
  }
}
