import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  TemplateRef,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';

@Component({
  selector: 'fn-icon',
  templateUrl: './fn-icon.component.html',
  styleUrls: ['./fn-icon.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FnIconComponent implements AfterViewInit {

  @Input() icon: string;

  // tslint:disable-next-line: no-inferrable-types
  @Input() lib = 'fa';

  @Input() style: string;

  @Input() size: string;

  @Input() animation: string;

  @Input() color: string;

  @Input() align: string;

  @ViewChild('content', {static: true}) content: ElementRef<any>;

  @ViewChild(TemplateRef, {static: true}) template: TemplateRef<any>;

  constructor(private change: ChangeDetectorRef) {
  }

  ngAfterViewInit() {
    this.contentIconCode();
  }

  contentIconCode() {
    if (this.content.nativeElement.innerText) {
      this.icon = this.content.nativeElement.innerText;

      if (this.isFullCode()) {
        this.lib = this.icon.substr(0, 2);

        this.getStyle();
      }
      this.change.detectChanges();
    }
  }

  isFullCode() {
    const space = this.icon.indexOf(' ');
    return space !== -1;
  }

  getStyle() {
    let fistSplit: number;
    let secondSplit: number;

    if (this.lib === 'fa') {
      fistSplit = 0;
      secondSplit = this.icon.indexOf(' ');
    }

    if (this.lib === 'ion') {
      fistSplit = this.icon.indexOf('-') + 1;
      secondSplit = this.icon.indexOf('-', fistSplit) - fistSplit;
    }

    this.setStyle(fistSplit, secondSplit);
  }

  setStyle(start: number, length: number) {
    this.style = this.icon.substr(start, length);

    switch (this.lib) {
      case 'dx':
        this.setIcon(16);
        break;
      case 'fa':
        const fistSplit = this.icon.indexOf('-') + 1;
        this.setIcon(fistSplit);
        break;
      case 'other':
        this.setIcon((start + length) + 1);
        break;
    }
  }

  setIcon(start: number) {
    this.icon = this.icon.substr(start);
  }

}
