import {Component, Input, ViewEncapsulation} from '@angular/core';
import {FnMenuItemState} from '../fn-stores/models/fn-menu.state';
import {FnMenuService} from '../fn-menu.service';

@Component({
  selector: '[fn-menu-item]',
  encapsulation: ViewEncapsulation.None,
  templateUrl: 'fn-menu.item.component.html'
})
export class FnMenuItemComponent {

  public renderMenuItem: FnMenuItemState;
  @Input() fnMenuItem: FnMenuItemState;

  constructor(private fnMenuService: FnMenuService) {
  }

  menuClickHandler(menuItem: FnMenuItemState) {
    this.fnMenuService.onMenuItemClick(menuItem.id);
  }
}
