import { Injectable } from "@angular/core";
import { Actions, Effect, ofType } from "@ngrx/effects"; 
import { mergeMap, map } from "rxjs/operators"; 
import {of} from 'rxjs';
import * as fnSessionAction from '../action/fn-seesion.action';
import { FnSession } from './fn.session.model';
import { FnSessionStorage } from '../fn-session.storage';

@Injectable()
export class FnSessionEffect {
    constructor(
        private action$: Actions
    ) { }

    @Effect()
    LoadSession$ = this.action$.pipe(
        ofType(fnSessionAction.FnSessionActionType.LoadSession),
        map((action: fnSessionAction.LoadSession) => action.payLoad),
        mergeMap((fnSession: FnSession) =>{
             FnSessionStorage.setFnSession(fnSession);
            return of(new fnSessionAction.LoadSessionSuccess(fnSession))       
        }   
        )
    ); 
    
}
