import {Component, Inject, ViewEncapsulation} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {DialogData} from '../fn-dialog.model';

@Component({
  templateUrl: 'fn-confirm-modal.component.html',
  styleUrls: ['fn-confirm-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FnConfirmModalComponent {


  constructor(public dialogRef: MatDialogRef<FnConfirmModalComponent>,
              @Inject(MAT_DIALOG_DATA) public dialogData: DialogData) {

  }

  close(isOkey: boolean = false) {
    this.dialogRef.close(isOkey);
  }
}
