import {
  Component,
  ViewEncapsulation,
  OnDestroy,
  forwardRef,
  ChangeDetectorRef,
  Input,
  TemplateRef
} from '@angular/core';
import {ENTER, LEFT_ARROW, RIGHT_ARROW, SPACE} from '@angular/cdk/keycodes';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'fn-switch',
  templateUrl: 'fn-switch.component.html',
  styleUrls: ['fn-switch.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => FnSwitchComponent),
      multi: true
    }
  ]
})
export class FnSwitchComponent implements ControlValueAccessor {


  constructor(private cdr: ChangeDetectorRef) {
  }

  checked = false;
  @Input() fnSize = '';
  @Input() isDisabled = false;
  @Input() checkedTemplate: TemplateRef<void>;
  @Input() unCheckedTemplate: TemplateRef<void>;

  onTouched: () => void;
  onChange: (value: boolean) => void = () => null;

  clickEvent(e: MouseEvent): void {
    e.preventDefault();
    if (!this.isDisabled) {
      this.updateValue(!this.checked);
    }
  }

  updateValue(value: boolean): void {
    if (this.checked !== value) {
      this.checked = value;
      this.onChange(this.checked);
    }
  }

  onKeyDown(e: KeyboardEvent): void {
    if (!this.isDisabled) {
      // tslint:disable-next-line: deprecation
      const keyCode = e.keyCode;
      if (keyCode === LEFT_ARROW) {
        this.updateValue(false);
        e.preventDefault();
      } else if (keyCode === RIGHT_ARROW) {
        this.updateValue(true);
        e.preventDefault();
      } else if (keyCode === SPACE || keyCode === ENTER) {
        this.updateValue(!this.checked);
        e.preventDefault();
      }
    }
  }


  writeValue(value: boolean): void {
    this.checked = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (_: boolean) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
    this.cdr.markForCheck();
  }


}
